.nav-bar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 0;
}

.nav-list {
  display: flex;
  align-items: center;
}

.nav-list li {
  margin-right: 10px;
}

.nav-list li a {
  color: darkcyan;
  font-size: 16px;
  line-height: 1.3;
  font-weight: 600;
  text-decoration: none;
}

.nav-list li a:hover {
  text-decoration: underline;
}
